import React, { useEffect, useState } from "react";
import "./leetcode.css"; // Create this for styling

const LeetCodeBar = () => {
  const [solved, setSolved] = useState(0);
  const [total, setTotal] = useState(1); // avoid divide by zero

  useEffect(() => {
    // Replace 'your-leetcode-username' with your actual username
    fetch(`https://leetcode-stats-api.herokuapp.com/NoobCoder3012`)
      .then((res) => res.json())
      .then((data) => {
        setSolved(data.totalSolved || 0);
        setTotal(data.totalQuestions || 1);
      })
      .catch((err) => console.error("LeetCode API error:", err));
  }, []);

  const percentage = Math.round((solved / total) * 100);

  return (
    <div className="leetcode-container">
      <h3> LeetCode Submissions  </h3>
      <div className="progress-bar">
        <div className="filled" style={{ width: `${percentage}%` }}></div>
      </div>
      <p>
        {solved} of {total} problems solved ({percentage}%)
      </p>
    </div>
  );
};

export default LeetCodeBar;
