import React, { Component } from "react";
import "./NewResume.css";
import Header from "../../components/header/Header";
import { Container, Row } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Particle from "../Resume/Particle";
import { greeting } from "../../portfolio";
import { AiOutlineDownload } from "react-icons/ai";
import { Document, Page, pdfjs } from "react-pdf";
import { Fade } from "react-reveal";
import TopButton from "../../components/topButton/TopButton";
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

class NewResume extends Component {
  constructor(props) {
    super(props);
    this.state = {
      width: 1200,
    };
  }

  componentDidMount() {
    this.updateDimensions();
    window.addEventListener("resize", this.updateDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions);
  }

  updateDimensions = () => {
    this.setState({ width: window.innerWidth });
  };

  render() {
    const theme = this.props.theme;
    const resumeLink = `${process.env.PUBLIC_URL}${greeting.resumeLink}`;
    const { width } = this.state;
    return (
      <div className="resume-main">
        <Header theme={this.props.theme} />
        <div
          className="basic-resume"
          style={{
            overflowY: "auto",
            scrollBehavior: "smooth",
            maxHeight: "100vh",
          }}
        >
          <Fade bottom duration={2000} distance="40px">
            <div className="heading-div">
              <div className="heading-img-div" style={{ marginTop: "-20px" }}>
                <img
                  alt="man_on_chair"
                  src={require("../../assests/images/Resume.svg")}
                ></img>
              </div>
              <div className="heading-text-div">
                <div className="main">
                  <div className="resume-content">
                    <Container fluid className="resume-section">
                      <Particle />
                      <Row
                        style={{
                          justifyContent: "center",
                          display: "flex",
                          marginTop: "20px",
                        }}
                      >
                        <Button
                          text="See My Resume"
                          newTab={true}
                          href={resumeLink}
                          theme={theme}
                        >
                          <AiOutlineDownload />
                          &nbsp;Download CV
                        </Button>
                      </Row>
                      <Row
                        style={{
                          justifyContent: "center",
                          display: "flex",
                          marginTop: "20px",
                        }}
                      >
                        <div
                          style={{ display: "flex", justifyContent: "center" }}
                        >
                          <Document file={resumeLink}>
                            <Page
                              pageNumber={1}
                              scale={width > 786 ? 1.1 : 0.6}
                            />
                          </Document>
                        </div>
                      </Row>
                    </Container>
                  </div>
                </div>
              </div>
            </div>
          </Fade>
        </div>
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

export default NewResume;
